.slider{
  height: 250px;
  margin: auto;
  margin-left: 0px;
  position: absolute;
  width: 100%;
  display: grid;
  top:35%;
  place-items: center;
  overflow: hidden;
  background-color: white;
border-width: 2px 1px 6px 1px;
border-style: solid;
border-color: #000000;
 border-top: 3px solid;
  border-bottom: 3px solid;
   height: 330px;
}

.slide-track:hover{
  animation-play-state: paused;
  
}

.slide-track {
  display: flex;
  width: calc(300px*10);
  animation: scroll 40s linear infinite;
  background-color: white;
  /* border-top: 1px solid;
  border-bottom: 1px solid; */
  
}
@media only screen and (max-width:480px) {
  .slide{
    width: 220px;
  }
  img{
    width: 200px;
    padding: 2px;
  }
}
@keyframes scroll{
  0% {
    transform: translateX(0);
  }
  100% {
    transform: translateX(calc(-300px*5));
  }
}

.slide {
  height: 200px;
  width: 550px;
  display: flex;
  align-items: center;
  padding: 16px;
  /* margin-top: 50px; */
  
  perspective: 100px;
}

.imgcam{
  height: 270px;
  width: 490px;
  transform: 0.5s;
  border: 1px solid;
  box-shadow: 2px 2px 3px black;
  padding: 5px;
  
}
img:hover{
  transform: translateZ(12px);
  border: none;
  padding: 5px;
  box-shadow: none;
}

.slider::before,
.slider::after{
  background: linear-gradient(to right, rgba(255,255,255,1) 0%,
  rgba(255,255,255,0) 100%);
  content: '';
  height: 100%;
  position: absolute;
  width: 2%;
  z-index: 2;
}

.slider::before{
  left: 0;
  top: 0;
}
.slider::after{
  right: 0;
  top: 0;
  transform: rotateZ(180deg);
}